<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";
import Upload from "@/components/Upload/Upload.vue";
import config from "../../../../public/config/customSetting.json";

export default {
  name: "joinQuitApply-component",
  components: { TableComponent, Pagination1, Upload },
  data() {
    return {
      form: {
        apply_Cateroy: "1", // 申請種類(預設為加入)
        org4Apply: "", // 各廠入會狀況
        authorization: [], // 資格

        fileName1: "", // 入會繳交附檔資料
        file1: "",
        fileName2: "", // 工作守則
        file2: "",
        fileName3: "", // 職業安全衛生組織核備函(1)
        file3: "",
        fileName4: "", // 職業安全衛生組織核備函(2)
        file4: "",
        fileName5: "", // 退會繳交附檔資料
        file5: "",
      },
      rules: {
        apply_Cateroy: [{ required: true, message: "必填", trigger: "change" }],
        org4Apply: [{ required: true, message: "必填", trigger: "change" }],
        authorization: [{ required: true, message: "必填", trigger: "change" }],
      },

      orgApplierTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "apply_Cateroy", label: "申請種類", width: "150" },
        { id: "protocolInfo", label: "組織名稱/附檔", width: "150" },
        { id: "apply_Date", label: "申請日期", width: "150" },
        { id: "authorization", label: "資格分類", width: "150" },
        { id: "status", label: "審核狀態", width: "150" },
        { id: "remark", label: "審核說明", width: "150" },
        { id: "name", label: "審核人員", width: "150" },
      ],
    };
  },
  computed: {
    // 依申請種類，列出已入會或未入會的廠商
    orgChoiceList() {
      if (this.form.apply_Cateroy == "1") {
        return this.$store.state.org_choicelist.filter((x) => !x.total);
      } else {
        return this.$store.state.org_choicelist.filter((x) => x.total);
      }
    },
    // 協議組織申請資料 (All)
    orgApplier() {
      return this.$store.state.org_applier;
    },
    // 協議組織申請資料 (單頁)
    orgApplierList() {
      return this.$store.state.org_applierlist;
    },
  },
  methods: {
    // 一更新申請種類，就清空所選廠商及所有附檔
    chgCategoryHandler() {
      this.form.org4Apply = "";
      this.chgAuthorizationHandler();
    },
    // 一更新主下包資格，即清空所有附檔
    chgAuthorizationHandler() {
      for (let i = 1; i <= 5; i++) {
        this.orgFileRmvHandler("", i);
      }
    },

    async orgFileChgHandler(file, index) {
      let raw = file[0].raw;
      let base64File = await this.toBase64(raw);

      // 取得檔名及 base64 轉檔，並移除欄位驗證
      this.form["fileName" + index] = file[0].name;
      this.form["file" + index] = base64File;
      this.$refs.form.clearValidate("fileName" + index);
    },
    orgFileRmvHandler(file, index) {
      this.form["fileName" + index] = "";
      this.form["file" + index] = "";
      this.$refs["fileRef" + index].$refs.Upload.clearFiles();
    },
    toBase64(file) {
      return new Promise((resolve, reject) => {
        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = (error) => reject(error);
      });
    },

    submitForm(formName) {
      // 入會及更新申請，且有主包者，file3/file4 需擇一上傳
      if (
        this.form.apply_Cateroy != "2" &&
        this.form.authorization.indexOf("F") != -1 &&
        !this.form.fileName3 &&
        !this.form.fileName4
      ) {
        this.$message({
          type: "error",
          showClose: true,
          message: "職業安全衛生組織核備函，請至少擇一上傳",
        });
      } else {
        this.$refs[formName].validate(async (valid) => {
          if (valid) {
            // 將主、下包拆成兩項
            this.form.po_Id = this.form.org4Apply.po_Id;
            this.form.po_Name = this.form.org4Apply.po_Name;
            this.form.f_Authorization =
              this.form.authorization.indexOf("F") != -1 ? "Y" : "";
            this.form.s_Authorization =
              this.form.authorization.indexOf("S") != -1 ? "Y" : "";

            try {
              const res = await this.$store.dispatch(
                "addOrgApplier",
                this.form
              );
              if (res.data.isOK) {
                this.$message({
                  type: "success",
                  duration: 5000,
                  showClose: true,
                  message: "申請成功",
                });

                // 清除表單，並更新申請列表 (呈現第一頁)
                this.$refs.form.resetFields();
                this.chgAuthorizationHandler();
                this.$store.commit("CURRENT_PAGE_INIT");
              }
            } catch (err) {
              console.log(err);
            }
          } else {
            this.$message({
              type: "error",
              showClose: true,
              message: "資料填寫錯誤",
            });
          }
        });
      }
    },

    // 從對應 controller 下載文件，並指定檔名
    async downloadPdf(item) {
      try {
        const res = await this.$store.dispatch("commonDownloadPdf", {
          file_id: item.file_Id,
          router_Name: config.getProtocolArea,
        });
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", `${item.file_Name}`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },
    async confirmHandler(id) {
      try {
        const res = await this.$store.dispatch("delOrgApplier", id);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: "5000",
            showClose: true,
            message: "刪除成功",
          });

          this.$store.commit("CURRENT_PAGE_INIT");
        }
      } catch (err) {
        console.log(err);
      }
    },

    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "org_applier",
        paramsListName: "org_applierlist",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得廠商及申請資料，顯示表格第一頁
    try {
      await this.$store.dispatch("getOrgForChoice");
      await this.$store.dispatch("getOrgApplier");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./joinQuitApply.html"></template>
